export interface ShowErrorActionProps {
  text: string;
  onClick?: () => void;
}

export interface ShowErrorProps {
  message: string;
  action?: ShowErrorActionProps;
  /**
   * For supported platforms, this will be used as the name for that show-error BI event.
   */
  name?: string;
}

export interface PlatformShowErrorProps extends ShowErrorProps {
  action?: {
    text: string;
    onClick: () => void;
  };
  requestId?: string | null;
}

export type ShowError = (
  error: unknown,
  props?: Partial<ShowErrorProps> | null,
) => void;

export type PlatformShowError = (props: PlatformShowErrorProps) => void;
